<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'About',
  props: {},
  data() {
    return {
      smallHeader: false,
      screenWidth: null,
      avatarName: "Yiwen"
    }
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth
        this.updateHeader()
      })()
    }
  },
  watch: {
    screenWidth: {
      handler: function() {
        this.updateHeader()
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    updateHeader() {
      let ratio = window.innerHeight / window.innerWidth
      this.heightWidthRatio = ratio
      // console.log(ratio)
      if (ratio > 1) {
        this.smallHeader = true
      } else {
        this.smallHeader = false
      }
    },
  }
}
</script>

